li a {
    color: black;
    font-size: 20px;
    text-decoration: none;
    padding: 10px 20px;
  
}
li a:hover{
    color: blue;
}
ul.nav-menu {

    display: flex;
    list-style: none;
    margin: 0;
    border-bottom: 4px solid rgb(71, 153, 235);
}
.sub-menu {
    display: none;
}
li.menu-item.menu-item2:hover .sub-menu{
    display: block;
    position: absolute;
  
   }
ul.dropdown{
    list-style: none;
    border-left: 2px solid rgb(71, 153, 235);
    border-right: 2px solid rgb(71, 153, 235);
    border-bottom: 2px solid rgb(71, 153, 235);
    padding: 0 5px;

   }
  